@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,500;0,600;0,700;1,400&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-montserrat: Montserrat;

  /* font sizes */
  --font-size-xl: 20px;
  --font-size-sm: 14px;
  --font-size-base: 16px;
  --font-size-5xl: 24px;
  --font-size-xs: 12px;
  --font-size-45xl: 64px;
  --font-size-13xl: 32px;
  --font-size-mini: 15px;
  --font-size-sm-7: 13.7px;
  --font-size-4xl: 23px;
  --font-size-12xl-2: 31.2px;

  /* Colors */
  --color-gray: #222;
  --color-darkgray: #a8a8a8;
  --color-white: #fff;
  --color-tomato: #fe6c37;
  --color-aliceblue: #e1f0ff;
  --color-royalblue: #1f49b8;

  /* Gaps */
  --gap-3xs: 10px;
  --gap-2xs: 11px;

  /* Paddings */
  --padding-3xs: 10px;
  --padding-xs-5: 11.5px;

  /* border radiuses */
  --br-3xs: 10px;
  --br-9xs: 4px;
  --br-xl: 20px;
  --br-41xl: 60px;
  --br-71xl: 90px;
  --br-8xs-6: 4.6px;
  --br-xs-5: 11.5px;
  --br-24xl-3: 43.3px;

  /* Effects */
  --drop-shadow: 0px 40px 40px rgba(0, 34, 131, 0.3);
}
